@use "./assets/scss/fonts" as *;
@use "./assets/scss/variables" as *;
@use "./assets/scss/mixins" as *;
* {
  margin: 0;
  padding: 0;
}
.mainpage {
  width: 100%;
  // max-width: 768px;
  .search {
    @include flex-layout(row, center);
    overflow: visible;

    .input {
      position: relative;
      transition: transform 0.3s ease;
      text-align: center;
      transform: translateY(-350%);
      @include flex-layout(row, center);
      width: 200px;
      z-index: 9999; // 지도 위로
      padding: 0.5rem;
      @include caption-r();
      border-radius: 15px;
      border: none;
      background-color: rgba(148, 148, 148, 0.25);
      box-shadow: 0 3px 3px rgba($color: #000000, $alpha: 0.33);
      backdrop-filter: saturate(100%) blur(5px);
      &.active {
        transform: translateY(-325px);
        opacity: 0;
      }
      &:focus {
        width: 250px;
        outline: none;
        border: 0.3px solid rgba(148, 148, 148, 0.45);
      }
    }
  }
}

// layout -sec3 처리
.layout-main {
  width: 100%;
  height: 100vh;
  padding: 0;
  .sec1 {
    display: none;
    flex: 0 0 360px;
    width: 360px;
    min-width: 360px;
  }
  .layout-outlet {
      flex: 1 1 auto;      
      min-width: 400px;         
    }
  .sec3 {
    display: none;
    flex: 0 0 400px;
    width: 400px;
    min-width: 400px;
  }
}

@include respond {
  .mainpage{
    .search{
    .input{
      display: none;
    }
  }
  }
  .layout-main {    
    @include flex-layout(row,space-around,flex-start,0);
    height: calc(100vh - 60px);
    .layout-outlet {
      min-width: 360px;
      margin: 0 auto;
      height: calc(100vh - 60px);
    }
    .sec1 {
      @include flex-layout(column, space-between, flex-end, 5rem );
      // height: 100vh;
      height: calc(100vh - 60px);
      @media (max-width:768px){
        .footer{
          display: none;
        }
      }
    }
    .sec3 {
      display: inline-block;
      width: 100%;
      max-width: 360px;
      justify-self: end;
      padding-right: 5rem;
      padding-top: 6rem;
    }
  }
}